<template>
  <div id="app">
    <router-view></router-view>
    <div class="top-box" v-show="flg" @click="cli">^</div>
  </div>
   
</template>

<script>

export default {
  data () {
    return {
        flg:false,
        scrollTop:0
    }
  },
  created () {
    this.say();
  },
  methods: {
    say(){
      window.addEventListener("scroll",()=>{
        this.scrollTop = document.body.scrollTop||document.documentElement.scrollTop;
        if(this.scrollTop>200){
          this.flg =  true;
        }else{
          this.flg = false
        }
      })
    },
    cli(){
      document.body.scrollTop=0;
      document.documentElement.scrollTop=0
    }
  },
  
}
</script>

<style>
  .top-box{
    width: 1rem;
    height: 1rem;
    background-color: aquamarine;
    border-radius: 50%;
    position: fixed;
    bottom: 2rem;
    right: 1rem;
    font-size: 0.5rem;
    line-height: 1rem;
    text-align: center;
    font-weight: bolder;
  }

</style>
