let Index = ()=>import("../component/index.vue");
    let Home = ()=>import("../component/index_child/home.vue")
    let Type = ()=>import("../component/index_child/Type.vue")
    let Prenson = ()=>import("../component/index_child/Prenson.vue")

let List = ()=>import("../component/List.vue");
let ser = ()=>import("@/component/ser.vue")


export default [{
    path:"/",
    component:Index,
    children:[{
        path:"",
        redirect:"home"
    },{
        path:"home",
        component:Home

    },{
        path:"type",
        component:Type
    },{
        path:"prenson",
        component:Prenson
    }]
},{
    path:"/list",
    component:List
},{
    path:"/ser",
    component:ser
}]