import Vue from 'vue'
import App from './App.vue';
import router from "./router/index";
import "./css/index.css";
function fun1(el,binding){
  var timer;
      el.ontouchstart=function(){
          timer=setTimeout(function(){
              binding.value.f.call(el,binding.value.h,binding.value.url);
          },binding.value.t||1000)
      }
      el.ontouchend=function(){
          clearTimeout(timer);
      }
}
Vue.directive("long",{
  inserted(el, binding) {
      fun1(el, binding)
  },
  update(el, binding) {
      fun1(el, binding)
  }
});

Vue.directive("lazy", {
  inserted(el, binding) {//DOM节点插入页面后
      var f = () => {
          var offsetTop = el.offsetTop;
          var H = window.innerHeight;
          var scrollTop = document.body.scrollTop || document.documentElement.scrollTop;
          if (H + scrollTop > offsetTop + 100) {
              el.src = binding.value;
              window.removeEventListener("scroll", f);
          }
      }
      window.addEventListener("scroll", f);
      f();
  },
  update(el, binding) {//当节点发生更新时
      var f = () => {
          var offsetTop = el.offsetTop;
          var H = window.innerHeight;
          var scrollTop = document.body.scrollTop || document.documentElement.scrollTop;
          if (H + scrollTop > offsetTop) {
              el.src = binding.value;
              window.removeEventListener("scroll", f);
          }
      }
      window.addEventListener("scroll", f);
      f();
  },
});

import { List } from 'vant';

Vue.use(List);

import { Toast } from 'vant';

Vue.use(Toast);


import { Search } from 'vant';

Vue.use(Search);

import { Swipe, SwipeItem } from 'vant';

Vue.use(Swipe);
Vue.use(SwipeItem);


new Vue({
  render: h => h(App),
  router,
}).$mount('#app')
